.footer{


    &__copyright{
        p,a{
        text-align: center;
        font-family: "ProximaNovaCond" , sans-serif;
        font-size: 15px;
        color: #808080;
        }
    }
    hr{
        margin: 0 auto;
        height: 14px;
        background: var(--c-primary);
        max-width: 721px;
        opacity: 1;
    }
    a{
        transition: 0.3s linear;
        &:hover{
            text-decoration: underline;
        }
    }
}