.pluses {
    display: flex;
    justify-content: space-between;
    padding: 0 5%;

    &__item {

        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        h5 {
            font-size: var(--fz-s);
            font-weight: 700;
            margin-bottom: 0;
        }

        svg {
            height: 50px;
            margin-bottom: 2rem;

            path {
                fill: var(--c-secondary);
            }
        }
    }
}