
html {
  box-sizing: border-box;
  font-size: var(--fz);
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: "BebasNeue", sans-serif;
  font-weight: 400;
  background-color: var(--c-white);
  color: #555555;

  &.no-scroll {
    overflow-y: hidden;
  }
}

h1,
h2 {
  font-family: "BebasNeue", sans-serif;
  font-weight: 700;
  margin: 0;
  padding: 0;
  color: #000;
  font-size: var(--fz-xl);
  line-height: 1.1;

  mark {
    margin: 0;
    padding: 0;
    color: var(--c-primary);
    background-color: transparent;
  }
}


h1 {
  color: #000;
  font-size: var(--fz-xxl);
}

h4 {
  font-family: "BebasNeue", sans-serif;
  font-size: var(--fz-xs);
  color: var(--c-primary);
  font-weight: 400;
  line-height: 1;
  margin-bottom: 1rem;
  text-transform: uppercase;
}

h2 {
  margin-bottom: var(--offset-m);
}


img {
  max-width: 100%;
}

a {
  text-decoration: none;
  font-family: "BebasNeue", sans-serif;
  color: var(--c-white);
  font-size: var(--fz-s);
  transition: color .2s linear;
  line-height: 1.2;

  &:hover {
    color: var(--c-primary);
  }
}

p {
  font-family: "BebasNeue", sans-serif;
  font-weight: 400;
  color: #000;
  font-size: var(--fz-s);
  line-height: 1.2;
}


.site-container {
  overflow: hidden;
  background: url(./../img/222.png) , #eff4fd;
}

.container--relative {
  position: relative;
}
.container {
  margin: 0 auto;
  padding: 0 (calc(var(--container-offset))) 0 var(--container-offset);
  
  max-width: var(--container-width);
}
.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.hover-line {
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 1px;
    background: var(--c-primary);
    position: absolute;
    bottom: -2px;
    left: 0;
    transform: scaleX(0);
    transition: transform .2s ease-in;
  }

  &:hover {
    &::after {
      transform: scaleX(1);
    }
  }
}
.shadow-top{
  position: relative;
  &::before{
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 50%;
    display: block;
    position: absolute;
    z-index: 5;
    background: rgb(255,255,255);
    background: linear-gradient(0deg, rgba(255,255,255,0) 80%, rgba(0,0,0,1) 100%);
    opacity: 0.6;
  }
}
.shadow-bootom-left{
  position: relative;
  &::before{
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
