.title {
  font-size: 65px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 60px;
}


.btn-5{
  position: relative;
  overflow: hidden;
  z-index: 1;
}
 .btn-5:after {
  content: "";
  background: #000;
  position: absolute;
  z-index: -1;
  left: -20%;
  right: -20%;
  top: 0;
  bottom: 0;
  transform: skewX(-45deg) scale(0, 1);
  transition: all 0.5s;
 }
 
 .btn-5:hover{
    color: var(--c-primary);
    &:after {
      transform: skewX(-45deg) scale(1, 1);
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
    }
 }
.popup__modal__content{
  border-radius: 0;
}











 











