$fa-font-path: "../../webfonts";


:root {
  --animate-delay: 2s;
  --content-width: 1500px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));


  --fz: 20px;
  --fz-xxl: 2.25rem; //45px
  --fz-xl: 1.8rem; //36px
  --fz-l: 1.20rem; //24px
  --fz-m: 1rem; //20px
  --fz-s: 0.8rem; // 16px
  --fz-xs: 0.7rem; // 14px

  --offset-xxl: 6rem;
  --offset-xl: 4.5rem;
  --offset-l: 3.5rem; 
  --offset-m: 3rem; 
  --offset-s: 2.75rem;
  --offset-xs: 2.5rem;  



  --c-primary: #fece00;
  --c-secondary: #3f5d76;
  --c-lsecondary: #5a758d;
  --c-white: #fff;
  --c-grey: #555555;
  --c-silver: #cccccc;
}
